<template>
  <div id="content">
    <back-header title="个人中心"
                 :rightText="rightBtn"
                 backData="go"
                 v-on:rightBtn="rightBtnClick">
    </back-header>

    <loading v-if="personalComplete && collectionComplete"></loading>

    <div v-else>
      <div class="personal-info">
        <div class="personal-icon">
          <img :src="personalData.avatar_url">
        </div>
        <div class="personal-information">
          <p class="personal-name">{{personalData.loginname}}</p>
          <p>注册于:{{personalData.create_at |time-filter}}</p>
        </div>
        <p class="personal-score">积分:{{personalData.score}}</p>
      </div>

      <table width="100%" class="personal-nav">
        <tbody>
        <tr>
          <td @click="tabNav('reply')" :class="{ 'personal-active' : tab == 'reply' }">回复</td>
          <td @click="tabNav('theme')" :class="{ 'personal-active' : tab == 'theme'}">主题</td>
          <td @click="tabNav('collection')" :class="{'personal-active': tab == 'collection'}">收藏</td>
        </tr>
        </tbody>
      </table>
      <other-list v-if="listData.length" :listData="listData"></other-list>
      <div v-else class="text-center margin-top-20">暂无内容</div>
    </div>


  </div>
</template>
<script>
  import backHeader from '../components/backHeader.vue'
  import dataUrl from '../api/DataUrl'
  import storage from '../api/Storage'
  import '../filters/filter'
  import otherList from '../components/OtherList.vue'
  import loading from '../components/loading.vue'

  export default {
    data() {
      return {
        rightBtn:'',
        personalData:{},
        tab:'reply',
        listData:[],
        collectionTopics:[],
        personalComplete:true,
        collectionComplete:true
      }
    },
    methods:{
      rightBtnClick() {
        sessionStorage.clear()
        this.$router.push('/')
      },
      // 切换选项卡
      tabNav (tabMsg) {
        let vm = this;
        if (vm.tab === tabMsg) return;
        vm.tab = tabMsg; // 切换tab

        switch (tabMsg) {
          case 'reply':
            vm.listData = vm.personalData.recent_replies
                break
          case 'theme':
            vm.listData = vm.personalData.recent_topics
                break
          case 'collection':
            vm.listData = vm.collectionTopics
                break
        }

      }
    },
    mounted() {
      let vm = this;
      let user = storage.user();

      let name = vm.$route.params.name;
      dataUrl.getUser(name).then((res)=>{
        vm.personalData = res.body.data;
        vm.listData = vm.personalData.recent_replies
        vm.personalComplete= false
      });

      dataUrl.getCollectionTopics(name).then((res)=>{
        vm.collectionTopics = res.body.data;
        vm.collectionComplete = false
      });

      // 当用户为自己时 显示退出按钮
      if (user && user.userName === name) {
        vm.rightBtn = '退出'
      }

    },
    components:{backHeader,otherList,loading}
  }

</script>

<style>
  .personal-info {
    overflow: hidden;
    padding: 10px;
    background-color: #F5F5DC;
  }
  .personal-icon {
    float: left;
    width: 50px;
    height: 50px;
  }

  .personal-icon>img{
    width: 100%;
    border-radius: 5px;
  }

  .personal-information{
    margin-left: 10px;
    float: left;
  }

  .personal-name {
    font-size: 18px;
    color: #6495ED;
    padding-bottom: 10px;
  }

  .personal-score {
    float: right;
    line-height: 50px;
    color: #FFB3FF;
  }

  .personal-nav {
    text-align: center;
    border-collapse:collapse;
    color: #888;
    background-color: #fff;
  }

  .personal-nav>tbody>tr>td {
    padding: 10px 0;
    border-bottom: 2px solid #e8e8e8;
  }

  .personal-active {
    color: #009FCC;
    border-bottom: 2px solid #009FCC!important;
  }

  .margin-top-20 {
    margin-top: 20px;
  }
</style>



