<template>
  <div id="content" :class="{'a':showRepliesBox}">
    <back-header title="主题" rightText="回复" backData="go" v-on:rightBtn="rightBtnClick"></back-header>

    <loading v-if="isShow"></loading>

    <div v-else>
      <div class="motif-top">
        <h3 class="text-center">{{data.title}}</h3>
        <div>
          <div class="author-icon">
            <img :src="author.avatar_url">
          </div>
          <div @click="goPersonal(author.loginname)">
            <div class="text-right">
              作者:{{author.loginname}}
            </div>
            <div class="text-right time-tab">发表于:{{data.create_at|time-filter}} <span class="tab">#{{data.tab|tab-filter}}#</span></div>
          </div>
        </div>
        <div class="markdown-body" v-html="data.content"></div>
        <div class="text-right collect-btn">
          <button v-if="allowCollectBtn" @click="collectBtn(data.is_collect)" class="btn" :class="[data.is_collect ? 'notCollect' : 'alrCollect']">{{data.is_collect ? '已收藏' : '收藏'}}</button>
          <button v-else class="btn" :class="[data.is_collect ? 'notCollect' : 'alrCollect']">{{data.is_collect ? '已收藏' : '收藏'}}</button>
        </div>
      </div>
      <div class="replies">
        <div class="replies-cell" v-for="(item,index) in repliesData">
          <div class="user-icon" @click="goPersonal(item.author.loginname)">
            <img :src="item.author.avatar_url">
          </div>
          <div class="replies-text">
            <div :index="'#'+(index+1)" class="author"><span>{{item.author.loginname}}</span> {{item.create_at|time-filter}}</div>
            <div class="markdown-body replies-body" v-html="item.content"></div>
          </div>
          <div class="text-right replies-footer">
            <span @click="upsBtn(item)" class="fabulous fabulous-icon" :class="{ 'onFabulous-icon' :item.ups.indexOf(userId) !=-1 }">{{item.ups.length ? item.ups.length: '&nbsp;'}}</span>
            <span @click="repliesClick(item)" class="replies-icon">&nbsp;</span>
          </div>
        </div>
      </div>

      <div v-show="showRepliesBox" @click.self="closeReplies" class="replies-mask"></div>
      <transition name="replies">
        <div v-show="showRepliesBox" class="replies-box">
          <div class="textarea"><textarea v-model="replies.text"></textarea></div>
          <div class="text-right replies-btn">
            <button @click="goReplies(replies)" class="btn">回复</button>
          </div>
        </div>
      </transition>

    </div>
  </div>
</template>

<script>
  import dataUrl from '../api/DataUrl'
  import backHeader from '../components/backHeader.vue'
  import '../filters/filter'
  import loading from '../components/loading.vue'
  import storage from '../api/Storage'
  export default {
    data() {
      return {
        data: {},
        author:{},
        isShow:true,
        allowCollectBtn:true,
        userId:'',
        showRepliesBox:false,
        replies:{
          text:'',
          id:''
        },
        repliesData:[]
      }
    },
    methods: {
      // 跳转个人中心
      goPersonal(val) {
        this.$router.push('/personal/'+val)
      },
      // 收藏按钮
      collectBtn(val) {
//        this.data.is_collect = !val
        let vm = this;
        vm.allowCollectBtn = false;
        let user = storage.user();

        if (user && user.token) {
            dataUrl.postCollection(vm.$route.params.id,user.token,val).then((res)=>{
              if (res.body.success) {
                vm.data.is_collect = !val;
                vm.allowCollectBtn = true;
              }
            })
        } else {
          this.$router.push('/login')
        }
      },
      // 点赞按钮
      upsBtn(item) {
        let user = storage.user();
        if (user && user.id) {
          let index = item.ups.indexOf(user.id);
          dataUrl.postReplyUps(item.id,user.token).then((res)=>{
            if (index === -1) {
              item.ups.push(user.id)
            } else {
              item.ups.splice(index,1);
            }
          },(err)=>{
            alert(err.body.error_msg)
          })
        } else {
          this.$router.push('/login')
        }
      },
      // 弹出回复框
      repliesClick(val) {
        let vm =this;
        vm.replies.text = '';
        vm.replies.id = '';
        let user = storage.user();
        if (user && user.id) {
          if (val) {
            vm.replies.text = '@'+val.author.loginname+' ';
            vm.replies.id = val.id
          }
          vm.showRepliesBox=true
        } else {
          vm.$router.push('/login')
        }
      },
      rightBtnClick(){
        this.repliesClick()
      },
      // 关闭回复框
      closeReplies() {
        this.showRepliesBox=false
      },
      // 立即回复
      goReplies(val) {
        let vm =this;
        let user = storage.user();
        if (val.text) {
          vm.showRepliesBox=false;
          let data = {
            topic_id:vm.$route.params.id,
            token: user.token,
            content: val.text,
            reply_id:val.id
          };
          dataUrl.postReply(data).then((res)=>{
            vm.getMotifContent()
          })
        }
      },

      // 获取数据
      getMotifContent() {
        let vm = this;

        dataUrl.getMotif(vm.$route.params.id).then((res)=>{
          let data =  res.body.data;
          vm.data = data;
          vm.author = data.author;
          vm.repliesData = data.replies
          vm.isShow = false
        })
      }
    },
    mounted() {
      let vm = this;
      vm.isShow = true;
      let user = storage.user();

      if (user && user.id) {
        vm.userId = user.id
      }
      vm.getMotifContent();
    },

//    beforeRouteEnter(to, from, next) {
//      next(vm=> {
//
//      })
//    },
    components:{
      backHeader,loading
    }
  }
</script>

<style>

  .motif-top {
    padding: 0 10px;
    border-bottom: 1px solid #e8e8e8;
  }

  .a {
    padding-bottom: 200px;
  }

  .tab {
    color: #FFB3FF;
  }

  .time-tab {
    padding: 5px 0 10px 0;
  }

  .replies {
    padding: 0 10px;
  }

  .replies-cell {
    padding: 5px 0;
    width: 100%;
    overflow: hidden;
    border-bottom: 1px solid #e8e8e8;
    height: 100%;
  }

  .user-icon {
    width: 40px;
    height: 100%;
    float: left;
    border-radius: 5px;
  }

  .user-icon img {
    width: 100%;
    border-radius: 5px;
  }

  .replies-text {
    padding-left: 45px;
  }

  .author {
    font-size: 12px;
    padding: 2px 0 5px 0;
  }

  .author:after {
    content: attr(index);
    float: right;
    color: #88AA00;
  }

  .author >span {
    color: #9F88FF;
    font-size: 14px;
  }

  .replies-body {
    padding-bottom: 15px;
  }

  .collect-btn {
    padding-bottom: 15px;
  }

  .collect-btn button {
    width: 80px;
    padding: 8px 0;
    color: #fff;
  }

  .notCollect{
    background-color: #99BBFF;
  }
  .alrCollect{
    background-color: #0066FF ;
  }

  .fabulous {
    display: inline-block;
    padding-left: 23px;
  }

  .fabulous-icon {
    background: url("../assets/images/good.png") no-repeat 0 3px;
    background-size: 20px;
    color: #515151;
  }

  .onFabulous-icon {
    background: url("../assets/images/onGood.png") no-repeat 0 3px;
    background-size: 20px;
    color: #36ab60;
  }

  .replies-icon {
    display: inline-block;
    padding-left: 30px;
    background: url("../assets/images/replies.png") no-repeat 0 2px ;
    background-size: 25px;
  }

  .replies-footer span{
    line-height: 30px;
    height: 30px;
    width: 20px;
    text-align: left;
  }

  .author-icon {
    float: left;
    width: 40px;
    height: 40px;
  }

  .author-icon>img {
    width: 100%;
    border-radius: 4px;
  }

  .replies-box{
    position: fixed;
    bottom: 0;
    width: 100%;
    height: 170px;
    background-color: #F8F8FF;
    -webkit-transition: all .25s ease;
    -moz-transition: all .25s ease;
    transition: all .25s ease;
  }

  .textarea {
    padding: 10px 15px;
  }

  .textarea>textarea {
    width: 100%;
    border: 1px solid #e8e8e8;
    height: 95px;
    font-size: 16px;
    resize: none;
  }

  .replies-btn {
    padding: 0 30px;
  }
  .replies-btn>button {
    width: 70px;
    height: 30px;
    background-color: coral;
    color: #fff;
    font-size: 14px;
  }

  .replies-enter,.replies-leave {
    transform: translateY(0px);
  }
  .replies-enter-active,.replies-leave-active {
    transform: translateY(200px);
  }

  .replies-mask {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 200px;
  }
</style>



