import Vue from 'vue'
import router from 'vue-router'
import storage from './Storage'

Vue.use(router)

const url = 'https://cnodejs.org/api/v1/';

export default {
  // 主题列表
  getTopics(options){
    return Vue.http.get(url + 'topics',options)
  },

  // 主题内容
  getMotif(id) {
    let user = storage.user();
    let token = '';
    if (user && user.token) {
      token = user.token
    }

    return Vue.http.get(url+ 'topic/'+ id,{
      params:{
        accesstoken:token
      }

    })
  },

  // 验证Token
  postAccessToken(token) {
    return Vue.http.post(url+'accesstoken',{
      accesstoken:token
    })
  },

  // 获取收藏主题
  getCollectionTopics(name) {
    return Vue.http.get(url+'topic_collect/'+name)
  },

  // 获取用户信息
  getUser(name) {
    return Vue.http.get(url+'user/'+name)
  },

  // 收藏/取消 主题
  postCollection(id,token,isCollect) {
    if (isCollect) {
      // 已收藏 需取消收藏
      return Vue.http.post(url+'/topic_collect/de_collect',{
        accesstoken:token,
        topic_id: id
      })
    } else {
      // 未收藏 需收藏
      return Vue.http.post(url+'/topic_collect/collect',{
        accesstoken:token,
        topic_id: id
      })
    }
  },

  // 点赞评论
  postReplyUps(id,token) {
    return Vue.http.post(url+'reply/'+id+'/ups',{
      accesstoken:token
    })
  },

  // 回复/评论 /topic/:topic_id/replies
  postReply(data) {
    return Vue.http.post(url+'topic/'+data.topic_id+'/replies',{
      accesstoken:data.token,
      content:data.content,
      reply_id:data.reply_id
    })
  },

  // 发布主题
  postTopics(data) {
    return Vue.http.post(url+'topics',{
      accesstoken:data.token,
      content:data.content,
      title:data.title,
      tab:data.tab
    })
  },

  // 消息messages
  getMessages(token) {
    return Vue.http.get(url+'messages', {
      params:{
        accesstoken:token
      }
    })
  },

  getMsgCount(token) {
    return Vue.http.get(url+'message/count', {
        params:{
          accesstoken:token
        }
      })
  },

  postMsgMark_all(token) {
    return Vue.http.post(url+'message/mark_all',{
      accesstoken:token,
    })
  }
}



