<template>
  <div class="login-bg">
    <back-header title="登陆" rightText="" backData="go"></back-header>
    <div class="login-from">
      <input v-model="token" type="text" placeholder="Access Token">
      <button @click="loginClick" class="btn">登 陆</button>
    </div>
  </div>
</template>

<script>
  import backHeader from '../components/backHeader.vue'
  import dataUrl from '../api/DataUrl'
  import storage from '../api/Storage'
  export default {
    data() {
      return {
        token: ''
      }
    },
    methods: {
      loginClick() {
        let vm = this;
        dataUrl.postAccessToken(vm.token).then((res)=>{
          let userData = {
            token: vm.token,
            avatar_url:res.body.avatar_url,
            id:res.body.id,
            userName:res.body.loginname
          };

          storage.setItem('user',userData);
          vm.$router.go(-1)

        },(err)=>{
          alert(err.body.error_msg)
        })
      }
    },
    mounted() {
//      dataUrl.postAccessToken()
    },
    components:{
      backHeader
    }
  }
</script>

<style>
  .login-bg {
    height: 100%;
    background-color: #FFFAFA ;
  }

  .login-from {
    position: fixed;
    top:50%;
    width: 100%;
    margin-top: -60px;
    /*padding: 0 30px 0 30px;*/
  }

  .login-from >input,.login-from >button {
    display: block;
    width: 80%;
    padding: 0;
    margin: 0 auto;
    border-radius: 4px;
  }

  .login-from >input {
    height: 25px;
  }

  .login-from >button {
    height: 30px;
    margin-top: 15px;
    background-color: #20B2AA   ;
    color: #fff;
  }
</style>



