<template>
    <header id="header">
        <div @click="back" class="left-icon back-icon"></div>
        <span>{{title}}</span>
        <div v-if="rightText" @click="rightBtn" class="right-icon reply-btn">{{rightText}}</div>
    </header>
</template>

<script>
  export default {
    props: ['title','rightText','backData'],
    methods: {
      back() {
        if (this.backData === 'go') {
          this.$router.go(-1)
        } else if (this.backData.type === 'replace') {
          this.$router.replace(this.backData.url)
        }
      },
      rightBtn() {
        this.$emit('rightBtn')
      }
    }
  }
</script>

<style>
    .back-icon {
        left: 0;
        background: url("../assets/images/back.png") no-repeat center;
        background-size: 22px 22px;
    }

    .reply-btn {
        right: 0;
        line-height: 44px;
        color: #00BBFF
    }
</style>



