<template>
  <div class="list">
    <div class="list-cell" v-for="item in listData" key="item.id" @click="goMotif(item.id)">
      <!--<router-link :to="'/motif/'+item.id">-->
      <h4 class="cell-title">
        {{item.title}}
      </h4>
      <div class="cell-msg">
        <div class="user-icon">
          <img :src="item.author.avatar_url">
        </div>
        <p class="user-name">{{item.author.loginname}}</p>
        <p class="cell-tab"><span>最后回复:{{item.last_reply_at| time-filter}}</span></p>
      </div>
      <div class="height-10"></div>
    </div>

  </div>
</template>
<script>
  export default {
    props: ['listData'],
    methods: {
      goMotif(id) {
        this.$router.push('/motif/'+id);
      }
    },
  }
</script>

<style>
  .height-10 {
    height: 10px;
  }

  .list {
    width: 100%;
    background-color: #e8e8e8;
    /*padding: 0 0 10px 0;*/
  }

  .list-cell {
    /*width: 100%;*/
    background-color: #fff;
    margin-top: 10px;
    padding: 0 8px;
    -webkit-box-shadow: 3px;
    -moz-box-shadow: 3px;
    box-shadow: 5px 0 5px 0 #aaa;
  }

  .cell-title {
    margin: 0;
    padding: 5px 0;
    line-height: 22px;
  }

  .cell-msg {
    overflow: hidden;
  }

  .user-icon {
    width: 30px;
    height: 30px;
    float: left;
  }
  .user-icon>img {
    width: 100%;
    border-radius: 4px;
    display:inline-block;
  }

  .user-name {
    line-height: 30px;
    font-weight: 400;
    float: left;
    margin-left: 5px;
  }

  .cell-tab {
    line-height: 30px;
    float: right;
    color: #FFB3FF;
  }

  .cell-tab >span{
    color: #00AA55;
  }
</style>



