import Vue from 'vue'

// tab 过滤器
Vue.filter('tab-filter', function (value) {
    switch (value) {
        case 'ask':
            value = '问答';
            break;
        case 'share':
            value = '分享';
            break;
        case 'job':
            value = '招聘';
            break;
        default:
            value = '无标签'

    }
    return value
});

Vue.filter('title-filter', function (value) {
  switch (value) {
    case '/home':
      value = '首页'
      break
    case '/collection':
      value = '收藏'
      break
    case '/msg':
      value = '消息'
      break
    default:
      value = '首页'
  }

  return value
});

// 时间->时间戳
Vue.filter('time-filter',function (value) {
    let timestamp2 = Date.parse(new Date(value));
    let currentTime = Date.parse(new Date());

    let dateDifference =currentTime-timestamp2;  //时间差的毫秒数
    if (dateDifference<=0) return '刚刚';

    const num1 = 12*30*24*3600*1000; // 计算年
    const num2 = 30*24*3600*1000; // 计算月
    const num3 = 24*3600*1000; // 计算天

    //计算相差年
    let years = Math.floor(dateDifference/num1);
    if (years) return years+'年前';

    // 计算月
    let monthSurplus = dateDifference%num1; // 剩余月
    let month = Math.floor(monthSurplus/num2);
    if (month) return month+'月前';

    //计算出相差天数
    let daysSurplus = monthSurplus%num2;
    let days=Math.floor(daysSurplus/num3);
    if (days) return days+'天前';

    // 计算相差小时
    let hoursSurplus=daysSurplus%num3;
    let hours=Math.floor(hoursSurplus/3600000);
    if (hours) return hours+'小时前';

    //计算相差分钟数
    let minutesSurplus = hoursSurplus % 3600000;
    let minutes = Math.floor(minutesSurplus / 60000);
    if (minutes) return minutes+'分钟前';

    //计算相差秒数
    // let secondsSurplus=minutesSurplus%60000;
    return '刚刚'
});



