<template>
    <div class="relative">
        <header id="header">
            <div @click="openMenu()" class="left-icon menu-icon"></div>
              <span>{{title | title-filter}}</span>
              <div @click="openWrite" class="right-icon edit-icon"></div>
        </header>
        <div id="content">

          <router-view></router-view>

        </div>


        <menu-nav :isShow="isShow" v-on:closeMask="closeMenu"></menu-nav>
      <post-article :isWrite="isWrite" v-on:closeWrite="closeWrite"></post-article>

    </div>
</template>

<script>

  import menuNav from '../components/menu.vue'
  import postArticle from '../components/PostArticle.vue'
  import storage from '../api/Storage'

  export default {
    data(){
      return {
        isShow:false,
        isWrite:false,
        title:this.$route.path
      }
    },
    methods:{
      openMenu(){
        this.isShow = true
      },
      closeMenu() {
        this.isShow = false
      },
      openWrite() {
        let user = storage.user();
        if (user && user.id) {
          this.isWrite = true;
          document.body.style.overflow = 'hidden'
        } else {
          this.$router.push('/login')
        }
      },
      closeWrite() {
        document.body.style.overflow = 'scroll';

        this.isWrite =false
      }
    },
    mounted () {

    },
    watch: {
      $route(val,old) {
        this.title= val.path
      }
    },
    components:{
      menuNav,postArticle
    },
//        beforeRouteEnter(to, from, next,go) {
//            next(vm=> {
//                let scrollTop = sessionStorage.getItem('scrollTop')
//                document.body.scrollTop = scrollTop
//            })
//        },
  }
</script>

<style>
  .relative {
    position: relative;
  }

  .menu-icon {
    left: 0;
    background: url("../assets/images/Category.png") no-repeat center;
    background-size: 22px 22px;
  }

  .edit-icon {
    right: 0;
    background: url("../assets/images/edit.png") no-repeat center;
    background-size: 22px 22px;
  }
</style>



