// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import VueRouter from 'vue-router'
import VueResource from 'vue-resource'
import { InfiniteScroll,Spinner } from 'mint-ui';

import App from './App'
import Hello from './views/Hello'
import Motif from './views/Motif'
import Login from './views/Login'
import Personal from './views/Personal'
import './assets/css/public.css'
import 'github-markdown-css'

Vue.use(VueRouter);
Vue.use(VueResource);
Vue.use(InfiniteScroll);
Vue.component(Spinner.name, Spinner);

// 路由配置
const cnodejs = resolve => {
  require.ensure(['./views/vueCnodejs.vue'], () => {
    resolve(require('./views/vueCnodejs.vue'));
  });
};

const home = resolve => {
  require.ensure(['./components/Home.vue'], () => {
    resolve(require('./components/Home.vue'));
  });
};

const msg = resolve => {
  require.ensure(['./views/Msg.vue'], () => {
    resolve(require('./views/Msg.vue'));
  });
};

const collection = resolve => {
  require.ensure(['./views/Collection.vue'], () => {
    resolve(require('./views/Collection.vue'));
  });
};


const routes = [
  {
    path: '/',
    component: cnodejs
  },{
    path: '/Hello/:id',
    component: Hello,
    children : [{
      path : '/Home',
      // 懒加载
      component: home,
    },{
      path: '/Collection',
      component:collection
    },{
      path: '/Msg',
      component: msg
    }]
  },{
    path: '/Motif/:id',
    component: Motif,
  },{
    path:'/Login',
    component: Login
  },{
    path:'/Personal/:name',
    component:Personal
  }
];

const router = new VueRouter({
  routes
});

// 拦截器
Vue.http.interceptors.push((request,next)=>{
  next((response) => {
    if (response.status===404) {
      alert(response.body.error_msg);
      // sessionStorage.clear()
      router.replace('/')
    }
  })
});

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App }
})



