<template>
  <div @click.self="closeMask" v-show="isMaskShow" class="mask">
    <transition name="menu">
      <div v-show="isShow" id="menu">
        <div class="menu-user" @click="userClick">
          <div class="menu-userIcon" :style="styleIcon"></div>
          <p class="userName">{{user.userName? user.userName: '未登录'}}</p>
        </div>
        <div class="menu-list">
          <div @click="menuCellClick('home')" class="menu-list-cell home-icon" :class="{ 'menu-active' :menuTab == '/home' }">首页</div>
          <div @click="menuCellClick('collection')" class="menu-list-cell collection-icon" :class="{ 'menu-active' :menuTab == '/collection' }">收藏</div>
          <div @click="menuCellClick('msg')"
               class="menu-list-cell msg-icon"
               :class="{ 'menu-active' :menuTab == '/msg' }">消息
            <span v-if="msgCount>0" class="msgCount">{{msgCount}}</span>
          </div>
        </div>
        <a class="me" href="https://github.com/Cyer/vue2.0-cnode" target="_Blank">@Cyer</a>
      </div>
    </transition>
  </div>
</template>

<script>
  import storage from '../api/Storage'
  import '../filters/filter'
  import dataUrl from '../api/DataUrl'

  export default {
    props:['isShow'],
    data () {
      return {
        isMaskShow:false,
        user:{},
        styleIcon:{},
        menuTab:this.$route.path,
        msgCount:0
      }
    },
    methods: {
      // 关闭菜单
      closeMask() {
        this.$emit('closeMask','');
      },
      // 点击user Cell
      userClick() {
        let vm = this;
        if (vm.user.userName) {
          // 已登录
          vm.$router.push('/personal/'+vm.user.userName)
        } else {
          // 未登录
          vm.$router.push('/login')
        }
      },
      menuCellClick(val) {
        if (val != 'home') {
          let user = storage.user();
          if (!user) {
            this.$router.push("/login");
            return
          }
        }

        if (val === 'msg') {
          this.msgCount = 0
        }

        this.menuTab = '/'+val;
        this.$emit('closeMask', val);
        this.$router.push("/"+val)


      }
    },
    mounted() {
      let vm = this;
      if (storage.getItem('user')) {
        vm.user = storage.getItem('user');
        vm.styleIcon = {
          'background-image': 'url("'+vm.user.avatar_url+'")'
        };

        dataUrl.getMsgCount(vm.user.token).then((res)=> {
          vm.msgCount = res.body.data
        })
      }
    },
    watch: {
      // 当menu完全隐藏后关闭遮罩层
      isShow: function (val, oldVal) {
        let vm = this;
        if (val) {
          vm.isMaskShow = val;
        } else {
          let timer = setTimeout(function () {
            vm.isMaskShow = val;
            clearTimeout(timer)
          },250)
        }
      }
    }
  }

</script>

<style>
  #menu {
    width: 170px;
    height: 100%;
    /*padding: 10px;*/
    background: #F8F8FF ;
    position: absolute;
    -webkit-transition: all .25s ease;
    -moz-transition: all .25s ease;
    transition: all .25s ease;
    /*left: -270px;  !* test fixed + scroll, on retire la position top *!*/
  }

  .menu-enter,.menu-leave {
    transform: translateX(0px);

  }
  .menu-enter-active,.menu-leave-active {
    transform: translateX(-170px);
  }

  .menu-user {
    overflow: hidden;
    /*background-color: #fff;*/
    padding: 10px;
    background-color: #F5F5DC ;
  }

  .menu-userIcon {
    float: left;
    width: 30px;
    height: 30px;
    background: url("../assets/images/noUser.png") no-repeat center;
    background-size: 30px;
    border-radius: 4px;
  }

  .userName {
    margin-left: 10px;
    line-height: 30px;
    float: left;
    color: #6495ED ;
  }

  .menu-list {

  }

  .menu-list-cell {
    padding: 7px;
    /*margin-top: 10px;*/
    height: 30px;
    line-height: 30px;
  }

  .menu-active {
    background-color: #D3D3D3;
  }

  .home-icon:before,.collection-icon:before,.msg-icon:before {
    display: inline-block;
    content: '';
    width: 20px;
    height: 30px;
    vertical-align: middle;
    margin-right: 10px;
  }

  .home-icon:before {
    background: url("../assets/images/ViewGallery.png") no-repeat 0 5px;
    background-size: 20px;
  }

  .collection-icon:before {
    background: url("../assets/images/favorite.png") no-repeat 0 3px;
    background-size: 20px;
  }
  .msg-icon:before {
    background: url("../assets/images/email.png") no-repeat 0 3px;
    background-size: 20px;
  }

  .msgCount {
    float: right;
    color: coral;
  }

  .me {
    position: absolute;
    bottom: 10px;
    right: 10px;
    color: #009fcc;
  }
</style>



