<template>
  <transition name="article">

    <div v-show="isWrite" class="write-article-box">
      <header id="header">
        <div @click="closeWrite" class="left-icon back-icon"></div>
        <span>发布</span>
        <!--<div @click="openWrite" class="right-icon edit-icon"></div>-->
      </header>
      <div class="write-header">
        <div class="write-title">
          <input v-model="title" type="text" placeholder="请输入标题...">
        </div>
        <div class="write-tab">
          <p class="gray">请选择标签：</p>
          <div class="text-center">
            <span @click="writeTab='share'" :class="{'write-tab-active':writeTab==='share'}">分享</span>
            <span @click="writeTab='ask'" :class="{'write-tab-active':writeTab==='ask'}">问答</span>
            <span @click="writeTab='job'" :class="{'write-tab-active':writeTab==='job'}">招聘</span>
          </div>
        </div>
      </div>
      <div class="write-content">
        <div>
          <textarea v-model="content" placeholder="内容..."></textarea>
        </div>
      </div>
      <div class="write-footer">
        <button @click="closeWrite" class="btn closeBtn">取消</button>
        <button @click="postArticle" class="btn postBtn">发布</button>
      </div>
    </div>

  </transition>
</template>

<script>
  import dataUrl from '../api/DataUrl'
  import storage from '../api/Storage'

  export default {
    props:['isWrite'],
    data() {
      return {
        writeTab:'share',
        title:'',
        content:''
      }
    },
    methods:{
      closeWrite() {
        this.$emit('closeWrite')
      },
      postArticle() {
        let vm = this;
        let user = storage.user();

        if (user && user.token) {
          let data = {
            token: user.token,
            tab: vm.writeTab,
            title: vm.title,
            content:vm.content
          };

          dataUrl.postTopics(data).then((res)=>{
            if(res.body.success) {
              document.body.style.overflow = 'scroll';
              this.$router.push('/motif/'+res.body.topic_id);
            } else {
              alert('发布失败')
            }
          },(err)=>{
            alert(err.body.error_msg)
          })

        } else {
          alert('token错误,请重新登录')
        }



      }
    }
  }
</script>

<style>

  .write-article-box {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
    z-index: 9999;
    overflow: hidden;
    background-color: #fff;
    -webkit-transition: all .25s ease;
    -moz-transition: all .25s ease;
    transition: all .25s ease;
  }

  .article-enter,.article-leave {
    transform: translateX(0px);

  }
  .article-enter-active,.article-leave-active {
    transform: translateX(100%);
  }

  .write-header {
    margin-top: 46px;
    padding: 10px;
  }

  .write-title ,.write-tab {
    padding: 4px 8px;
    border-bottom: 1px solid #e8e8e8;
  }

  .write-title>input {
    border: 0;
    outline:none;
    font-size: 16px;
    width: 100%;
  }

  .gray {
    color: #888;
  }

  .write-tab>p {
    padding: 5px 0;
  }
  .write-tab>div{
    padding: 10px 0;
  }
  .write-tab span {
    color: #888;
    border: 1px solid #888;
    border-radius: 4px;
    padding: 5px 10px;
    margin: 0 10px;
  }

  .write-tab-active {
    color: #0066FF !important;
    border: 1px solid #0066FF !important;
  }

  .write-content {
    position: fixed;
    left: 0;
    top: 165px;
    bottom: 50px;
    width: 100%;
  }

  .write-content>div {
    padding: 0 10px;
    height: 100%;

  }

  .write-content>div>textarea {
    width: 100%;
    height: 100%;
    resize: none;
    outline:none;
    border: 0;
    font-size: 16px;
  }

  .write-footer {
    position: fixed;
    left: 0;
    bottom: 0;
    height: 40px;
    width: 100%;
    border-top: 1px solid #e8e8e8;
  }

  .closeBtn,.postBtn {
    width: 60px;
    height: 26px;
    margin-top: 7px;
    color: #fff;

  }

  .closeBtn {
    background-color: coral;
    float: left;
    margin-left: 10px;
  }
  .postBtn{
    background-color: #5555FF ;
    float: right;
    margin-right: 10px;
  }
</style>



